import { React, useState, useEffect,useContext } from 'react';
import { useParams, Link,useNavigate } from 'react-router-dom';
import Sidebar from '../components/Sidebar';
import './trending.css'
import TrendingViodecard from '../components/TrendingViodecard';
import axios from "axios";
import VideoContext from '../Context/VideoContext';


const Searchpage = () => {

    const { keyword } = useParams()
    const {Videostate,changeVideo}=useContext(VideoContext)
    const [displayArray, setDisplayArray] = useState([])
    const navigate = useNavigate();
    const [Vid,setVid]=useState({})

    useEffect(() => {
        const newInputArray = keyword.split(" ");
        let newInput = newInputArray[0];
        for (let i = 1; i < newInputArray.length; ++i)
            newInput = newInput + "+" + newInputArray[i];
        // console.log(newInput)
        
        axios.request(`http://localhost:5000/search/${newInput}`).then(function (response) {
                // console.log(response.data)
                let data = response.data;
                setDisplayArray(data)
                
            }).catch(function (error) {
                console.error(error);
            });

    }, [keyword])
   async function changeVideoState(video,keyid){

        let newVid={};

   
                newVid= {
                    "videoId":keyid,
                    "image1": video.bestThumbnail?.url,
                    "image2": video.author?.bestAvatar?.url,
                    "title" :video.title,
                    "channel":video.author?.name,
                    "views": video.views,
                    "time":video.uploadedAt,
                    }
                    changeVideo(newVid);

    navigate(`/watch/${keyid}`);
    }


    return (
        <div className='trending'>
            <Sidebar className='Home_sidebar' />


            <div className='trending_videos'>
                {displayArray ? displayArray.map((video) => {
                    if(video.type=='video')
                   { let vidurl=video.url;
                    let urlarray=vidurl.split('=')
                    
                    return <div onClick={()=>changeVideoState(video,urlarray[1])}><TrendingViodecard  image1={video.bestThumbnail?.url} image2={video.author?.bestAvatar?.url} title={video.title} channel={video.author?.name} time={video.uploadedAt} views={video.views} /></div>
                    
                }}) : null}


            </div>
        </div>
    )
}

export default Searchpage;


