import {React,useEffect,useState,useContext} from 'react'
import { useParams, Link,useNavigate } from 'react-router-dom';
import Sidebar from '../components/Sidebar'
import './commentpage.css'
import Videocard from '../components/Videocard'
import CommentCard from '../components/CommentCard';
import axios from 'axios'
import cloneContext from '../Context/CloneContext';
import VideoContext from '../Context/VideoContext';

const LikePage = () => {

    const [Likes_Array,setLikes_Array]=useState([])
    const navigate = useNavigate();
    const {state,changeUser}=useContext(cloneContext)
    const{username}=state
    const {Videostate,changeVideo}=useContext(VideoContext)

useEffect(()=>{
    
    axios.request(`http://localhost:5000/api/get_likes/${username}`).then(function (response) {
        // console.log(response.data)
        let data = response.data;
        setLikes_Array(data);
        console.log(Likes_Array)
    }).catch(function (error) {
        console.error(error);
    });
},[])

function Navigate_user(liked_video)
{
    const newVid= {
        "image1": liked_video.image1,
        "image2": liked_video.image2,
        "title" :liked_video.title,
        "channel":liked_video.channel,
        "views": liked_video.views,
        "videoId":liked_video.videoid,
         }

    changeVideo(newVid);
    navigate(`/watch/${liked_video.videoid}`);
}
  return (
    <div className='Home_container'>
      <Sidebar className='Home_sidebar ' />
      <div className='recommended'>
      <div className='Recommended_videos cmnt_videos'>
       {
        Likes_Array? Likes_Array.slice(0).reverse().map((liked_video)=><div className='cmt-crd' onClick={()=>Navigate_user(liked_video)}><CommentCard   image1={liked_video.image1} image2={liked_video.image2} title={liked_video.title} channel={liked_video.channel} time={liked_video.time} views={liked_video.views}/></div>):null
        }
      
      </div>
    </div>
    </div>
  )
}

export default LikePage
