import {React,useEffect,useState,useContext} from 'react'
import { useParams, Link,useNavigate } from 'react-router-dom';
import Sidebar from '../components/Sidebar'
import './commentpage.css'
import Videocard from '../components/Videocard'
import CommentCard from '../components/CommentCard';
import axios from 'axios'
import cloneContext from '../Context/CloneContext';
import VideoContext from '../Context/VideoContext';

const CommentPage = () => {

    const [Commented_Array,setCommented_Array]=useState([])
    const navigate = useNavigate();
    const {state,changeUser}=useContext(cloneContext)
    const{username}=state
    const {Videostate,changeVideo}=useContext(VideoContext)

    

useEffect(()=>{
    
    axios.request(`http://localhost:5000/api/get_comments/${username}`).then(function (response) {
        // console.log(response.data)
        let data = response.data;
        setCommented_Array(data);
        // console.log(Commented_Array)
    }).catch(function (error) {
        console.error(error);
    });
},[])

function Navigate_user(commented_video)
{
    const newVid= {
        "image1": commented_video.image1,
        "image2": commented_video.image2,
        "title" :commented_video.title,
        "channel":commented_video.channel,
        "views": commented_video.views,
        "videoId":commented_video.videoid,
         }

    changeVideo(newVid);
    navigate(`/watch/${commented_video.videoid}`);
}
  return (
    <div className='Home_container'>
      <Sidebar className='Home_sidebar ' />
      <div className='recommended'>
      <div className='Recommended_videos cmnt_videos'>
       {
        Commented_Array? Commented_Array.slice(0).reverse().map((commented_video)=><div className='cmt-crd' onClick={()=>Navigate_user(commented_video)}><CommentCard image1={commented_video.image1} image2={commented_video.image2} title={commented_video.title} channel={commented_video.channel} time={commented_video.time} views={commented_video.views}/></div>):null
        }
      
      </div>
      </div>
    </div>
  )
}

export default CommentPage
