import {React} from 'react';
import './Sidebar.css'
import Siderow from './Siderow'
import HomeIcon from '@mui/icons-material/Home';
import WhatshotIcon from '@mui/icons-material/Whatshot';
import HistoryIcon from '@mui/icons-material/History';
import ThumbUpOffAltIcon from '@mui/icons-material/ThumbUpOffAlt';
import CommentIcon from '@mui/icons-material/Comment';
import InfoIcon from '@mui/icons-material/Info';

const Sidebar = () => {

  return (
    <div className='sidebar'>
      <Siderow  title='home' Icon={HomeIcon} selected={false}/>
      <Siderow  title='trending' Icon={WhatshotIcon} selected={false}/>
      <Siderow  title='History' Icon={HistoryIcon} selected={false}/>
      <Siderow title='Likes' Icon={ThumbUpOffAltIcon} selected={false}/>
      <Siderow title='Comments' Icon={CommentIcon} selected={false}/>
      <Siderow title='about' Icon={InfoIcon} selected={false}/>
      
      

    </div>
  )
}

export default Sidebar
