import { React, useState, useContext } from 'react';
import Y_Logo from '../images/Logo_of_YouTube_(2015-2017).svg.webp'
import SearchIcon from '@mui/icons-material/Search';
import LogoutIcon from '@mui/icons-material/Logout';


import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import './Nav.css'
import {useNavigate} from 'react-router-dom'
import cloneContext from '../Context/CloneContext';


const Nav = () => {


    const { state,changeUser,interestState,changeInterest } = useContext(cloneContext)

    const {username}=state

    const [input_text, setInput_text] = useState("");
    const navigate=useNavigate();
   function navigate_user(){
        navigate(`/search/${input_text}`);
   }


    return (
        <div className={`navbar ${!username&&"hide"}`} >


        <div className='second'>
                <input placeholder='Search' type='text' onKeyDown={(e)=>{if(e.key==='Enter')navigate_user()}} onChange={(e) => setInput_text(e.target.value)} />
               {/* <button className='magnifine-icon' onClick={navigate_user}><SearchIcon /></button> */}
            </div>
            <div className='third'>
               <div className='user_icon'> 
               <AccountCircleIcon className='nav_icon' />
                <p>{username}</p>
                </div>
                {/* <LogoutIcon className='nav_icon' /> */}

            </div>

           
           

          
        </div>
    )
}

export default Nav
