import CloneContext from "./CloneContext";
import {React,useState,useEffect} from 'react'

const CloneState = (props) => {
    const [user,setUser]=useState(false)
    const state={username:user}
    const [interest,setInterest]=useState("")
    const interestState={user_interest:interest}
    
    function changeUser(newUser)
    {
        setUser(newUser)

    }
    function changeInterest(newInterest)
    {
      setInterest(newInterest)
    }
  return (
    <CloneContext.Provider value={{state,changeUser,interestState,changeInterest}}>
      {props.children}
      </CloneContext.Provider>
  )
}

export default CloneState
