import React from 'react'

import './CommentCard.css'

const CommentCard = ({image1,image2,title,channel,time,views}) => {
  
  return (
    <div className='videoCard'>
    <img className='videoCard_thumb' src={image1} alt='' />
    <div className='videoCard__info'>
      <img className='videoCard__avatar' src={image2} alt=''></img>
      <div className='videoCard__text'>
        <h4>{title}</h4>
        <p>{channel}</p>
        <p>{views} -{time}</p>
      </div>
    </div>  
    </div>
  )
}

export default CommentCard





