import {React,useEffect,useState,useContext} from 'react'
import { useParams, Link,useNavigate } from 'react-router-dom';
import Sidebar from '../components/Sidebar'
import './commentpage.css'
import Videocard from '../components/Videocard'
import CommentCard from '../components/CommentCard';
import axios from 'axios'
import cloneContext from '../Context/CloneContext';
import VideoContext from '../Context/VideoContext';

const HistoryPage = () => {

    const [History_Array,setHistory_Array]=useState([])
    const navigate = useNavigate();
    const {state,changeUser}=useContext(cloneContext)
    const{username}=state
    const {Videostate,changeVideo}=useContext(VideoContext)

useEffect(()=>{
    
    axios.request(`http://localhost:5000/api/get_history/${username}`).then(function (response) {
        // console.log(response.data)
        let data = response.data;
        setHistory_Array(data);
        // console.log(Likes_Array)
    }).catch(function (error) {
        console.error(error);
    });
},[])

function Navigate_user(history_video)
{
    const newVid= {
        "image1": history_video.image1,
        "image2": history_video.image2,
        "title" :history_video.title,
        "channel":history_video.channel,
        "views": history_video.views,
        "videoId":history_video.videoid,
         }

    changeVideo(newVid);
    navigate(`/watch/${history_video.videoid}`);
}
  return (
    <div className='Home_container'>
      <Sidebar className='Home_sidebar ' />
      <div className='recommended'>
      <div className='Recommended_videos cmnt_videos'>
       {
  History_Array? History_Array.slice(0).reverse().map((history_video)=><div className='cmt-crd' onClick={()=>Navigate_user(history_video)}><CommentCard image1={history_video.image1} image2={history_video.image2} title={history_video.title} channel={history_video.channel} time={history_video.time} views={history_video.views}/></div>):null
}

      
</div>
      </div>
    </div>
  )
}


export default HistoryPage


