import { React, useState, useEffect, useContext } from 'react';
import { Routes, Route, useNavigate, Link } from 'react-router-dom';
import './Login.css'
import axios from "axios";
import cloneContext from '../Context/CloneContext';
import CheckIcon from '@mui/icons-material/Check';
import ClearIcon from '@mui/icons-material/Clear';
const Login = () => {
  const [Username_input, setInput_username] = useState("");
  const [Password_input, setInput_password] = useState("");
  const [Unique_username, setUnique_username] = useState('');
  const [Unique_password, setUnique_password] = useState('');
  const { state,changeUser,interestState,changeInterest } = useContext(cloneContext)
  const navigate = useNavigate();



  async function validate_user() {

    let value = false;
    let newInterest=""

    
    await axios.request(`http://localhost:5000/api/getUser/${Username_input}/${Password_input}`).then(function (response) {
      value = response.data.success
      newInterest=response.data.interest


    }).catch(function (error) {
      console.error(error);
    });
    if (value === true) {
      changeUser(Username_input)
      changeInterest(newInterest)
      navigate(`/${Username_input}/home`);
    }

  }

  async function demo_signin()
  {

   
    let newInterest=""

    await axios.request('http://localhost:5000/api/getUser/demo/abcd').then(function (response) {
      
      newInterest=response.data.interest


    }).catch(function (error) {
      console.error(error);
    });
    
      changeUser('demo')
      changeInterest(newInterest)
      navigate('/demo/home');
    

  }


  async function check_username(value)
    {
      await axios.request(`http://localhost:5000/api/unique_username/${value}`).then(function (response) {
        if(response.data.success==false) 
        {
          setUnique_username(true)
          setInput_username(value)
        }
        else{
          setUnique_username(false)
        }
  
  
      }).catch(function (error) {
        console.error(error);
      });
      

    }


    async function check_password(value)
    {
      await axios.request(`http://localhost:5000/api/getUser/${Username_input}/${value}`).then(function (response) {
        if(response.data.success==true) 
        {
          setUnique_password(true)
          setInput_password(value)
        }
        else{
          setUnique_password(false)
        }
  
  
      }).catch(function (error) {
        console.error(error);
      });
      

    }
 
   

  return (
    

  
    
    <div className='Login-container'>
    
    
      <div className='input-tags'>
      <div className='input-tags-text'>Login To Your Account</div>
        <div className='username-box'><input type='text' placeholder='Enter your username' onChange={(e) => check_username(e.target.value)}></input>
        {Username_input&&Unique_username&&<CheckIcon className='correct-icon' />}
     {Username_input&&!Unique_username&&<ClearIcon className='wrong-icon' />}
     </div>
        <div className='username-box'><input type='text' placeholder='Enter your password' onChange={(e) => check_password(e.target.value)}></input>
        {Password_input&&Unique_password&&<CheckIcon className='correct-icon' />}
     {Password_input&&!Unique_password&&<ClearIcon className='wrong-icon' />}</div>
      </div>
      <button onClick={validate_user}>Sign-in</button>
      <Link to='/signup'><p>Register</p></Link>

      <button onClick={demo_signin}>Continue as guest</button>
      {/* <div className='more-text'>Demo username-'demo'<br></br>Demo password-'abcd'</div> */}
      </div>


    
 


  )
}

export default Login
