import {React,useContext,useEffect} from 'react'
import axios from "axios";


import './trendingvideocard.css'
const TrendingViodecard = ({ image1, image2, title, channel, time, views }) => {

    
    

   
 
    return (
        <div className='trending_card'>
            <div className='trending_thumbnail'>
                <img src={image1} />
            </div>
            <div className='trending_text'>
                <h3>{title}</h3>
                <p className='trending_time'>{time}</p>
                <p className='trending_views'>{views} views</p>
                <img className='logo-img' src={image2} />
                <p>{channel}</p>
            </div>
            
        </div>
    )
}

export default TrendingViodecard
