import {React,useContext,useEffect} from 'react'
import axios from "axios";
import './morevideo.css'
const MoreVideo = ({ image1, image2, title, channel, time, views }) => {

    return (
        <div className='more-trending_card'>
            <div className='more-trending_thumbnail'>
                <img src={image1} />
            </div>
            <div className='more-trending_text'>
                <h3>{title}</h3>
                <p className='more-trending_time'>{time}</p>
                <p className='more-trending_views'>{views} views</p>
               
                {/* <p>{channel}</p> */}
            </div>
            
        </div>
    )
}

export default MoreVideo