import {React,useContext} from 'react'
import './FooterIcons.css'
import {Link} from 'react-router-dom'
import cloneContext from '../Context/CloneContext';

const FooterIcons = ({title,Icon,selected}) => {
    const {state,changeUser}=useContext(cloneContext)
    const{username}=state
  return (
    <div>
       
      <Link to={`/${username}/${title}`} className='siderow_title'>
      <Icon  className='foot-icon'/>
      </Link>
    </div>
  )
}

export default FooterIcons
