import React from 'react'
import './About.css'
import Image from '../images/image1.png'
import Sidebar from '../components/Sidebar'

const About = () => {
   
  return (
    <>
    <div class="about-section">
	<Sidebar />
		<div class="about-container">
		<div class="about-image-section">
				<img src={Image} alt=""/>
			</div>
			<div class="about-content-section">
				<div class="about-title">
					<h1>About</h1>
				</div>
				<div class="about-content">
					<h2>Created by- <span>Sumit Thakur</span></h2>
					<p>This is a react clone created only as a college project. The clone has all the P0 features as a Youtube clone. A user can create his/her account, can surf through entire Youtube video collection and can watch any video he/she wants. The database stores all the user activity like watch history, like and comment activities. Moreover the clone also has a Trending section where you will find all the trending videos related to your interest  .</p>
					<p>Apis used in this project- <a href='https://rapidapi.com/marindelija/api/youtube-search-results/'>YouTube Search Results</a> and <a href='https://rapidapi.com/pyuser/api/youtube-trending/'>Youtube Trending</a>.</p>
					<div class="about-button">
                        <button type='button'>Linkdin</button>
					</div>
				</div>
		
			</div>
			
		</div>
	</div>
</>
  )
}

export default About

