import { React, useState, useContext } from 'react';
import FooterIcons from './FooterIcons';
import './Footer.css'
import HomeIcon from '@mui/icons-material/Home';
import WhatshotIcon from '@mui/icons-material/Whatshot';
import HistoryIcon from '@mui/icons-material/History';
import ThumbUpOffAltIcon from '@mui/icons-material/ThumbUpOffAlt';
import InfoIcon from '@mui/icons-material/Info';
import CommentIcon from '@mui/icons-material/Comment';
import cloneContext from '../Context/CloneContext';

const Footer = () => {
  const { state,changeUser,interestState,changeInterest } = useContext(cloneContext)
  const {username}=state
  return (
    <div className={`footer hide-footer ${!username&&"hide"}`}>
      <FooterIcons className='footer-icon' title='home' Icon={HomeIcon} selected={true}/>
      <FooterIcons className='footer-icon' title='trending' Icon={WhatshotIcon} selected={false}/>
      <FooterIcons className='footer-icon' title='History' Icon={HistoryIcon} selected={false}/>
      <FooterIcons className='footer-icon' title='Likes' Icon={ThumbUpOffAltIcon} selected={false}/>
      <FooterIcons className='footer-icon' title='Comments' Icon={CommentIcon} selected={false}/>
      <FooterIcons className='footer-icon' title='about' Icon={InfoIcon} selected={false}/>
    </div>
  )
}

export default Footer
