import {React,useContext} from 'react'
import './siderow.css'
import {Link} from 'react-router-dom'
import cloneContext from '../Context/CloneContext';

const Siderow = ({title,Icon,selected}) => {
  const {state,changeUser}=useContext(cloneContext)
    const{username}=state
    
  return (
    <div className= {`siderow ${selected&&"selected"}`}>

<Icon className='siderow_icon'/>
      <Link to={`/${username}/${title}`} className='siderow_title'>
      
      {title}
      </Link>
     
    </div>
  )
}

export default Siderow
